@use 'variables'

body
    font-size: 16px
    max-width: 1200px
    line-height: 1.3em

.wrapper
    padding-top: 45px

nav, nav h1
    font-size: 16px
    display: inline-block

nav
    border-bottom: 3px solid black
    padding: 0 10px
    width: 100%
    position: fixed
    background-color: transparent
    height: 40px
    z-index: 100
    top: 5px
    left: 0
    font-family: "Cardo",serif
    font-variant-caps: small-caps
    text-align: center
    &:before
        content: ""
        position: absolute
        width: 100%
        height: 100%
        top: 0px
        left: 0px
        z-index: -1
        // From: https://codepen.io/AgnusDei/pen/NWPbOxL
        box-shadow: 2px 3px 20px black, 0 0 12px #8f5922 inset
        background: #fffef0
        /* v2.1 : borders effect with SVG : try to play with scale to change them */
        filter: url(#wavy2)
        /* v2.2 : Noise added for a vellum paper effect */
        background-image: url(data:image/png;base64,)
    ul
        background: transparent
        margin-top: 12px
    a, a:link, a:visited, h1 a, h1 a:link, h1 a:visited
        font-weight: bolder
        color: var(--rule-red)
        text-decoration: underline var(--title-red)
    a:hover, h1 a:hover
        text-decoration: underline var(--title-red) wavy
    li
        display: inline-block
        &:not(:last-of-type)::after
            content: "|"
            padding: 0 9px
            display: inline-block

p
    margin-bottom: 1em

header h1
    font-size: 2.3em
    line-height: 1.2em

.two-columns table
    margin: 12px 0
    overflow-x: auto
    thead
        font-weight: 800
        th
            padding: 0 6px 4px
            border: solid var(--title-red)
            border-width: 0 thin thick

main
    h1, h2, h3, h4, h5, h6
        font-size: larger
    h2
        font-size: x-large
    & > h2
        font-size: 1.8em

#resource-list
    font-size: 25px
    font-weight: bolder
    a
        text-decoration: underline
    li
        margin-bottom: 10px

a.read-more-link
    font-size: 20px
    text-decoration: underline

.pagination-block
    margin-top: 20px
    margin-bottom: 20px

.disabled-link
    color: grey
    text-decoration: none
    &:hover
        text-decoration: none
        color: grey

.pagination
    margin-left: 25px
    // background: ghostwhite
    float: left
    width: 100%
    padding: 8px 0
    a
        font-size: 16px
        text-align: center
        width: 130px
        background-color: #171717
        color: #fdfdfb
        &.disabled-link
            background-color: rgba(220, 220, 220, .5)
            color: rgba(0, 0, 0, .3)
        &:hover:not(.disabled-link)
            background: lightblue
            color: black
        &:hover.disabled-link
            color: rgba(0, 0, 0, .3)
        &:first-child
            border-right: 0px solid black
            margin-left: 10px
            float: left
            box-shadow: -10px 5px 8px rgba(0, 0, 0, .5)
        &:last-child
            border-left: 0px solid black
            float: right
            margin-right: calc(25px + 10px)
            box-shadow: 10px 5px 8px rgba(0, 0, 0, .5)

footer
    margin-top: 25px
    font-size: smaller
    text-align: center
    font-weight: normal

@media print
    nav, footer
        display: none

@media (max-width: 1400px)
    .two-columns .dnd-table-container
        box-sizing: content-box
        margin: 12px 0
        width: 95%
        overflow-x: auto
        display: block
        max-width: -moz-fit-content
        max-width: fit-content
        white-space: nowrap
        scrollbar-color: var(--rule-red) var(--item-table-pink)
        scrollbar-width: auto
        &::-webkit-scrollbar
            height: 15px
        /* Track */
        &::-webkit-scrollbar-track
            background: var(--item-table-pink)
        &::-webkit-scrollbar-thumb
            background: var(--rule-red)
            border-radius: 10px
            height: 15px

@media (max-width: variables.$mobile-width)
    body
        width: 75%
    .two-columns
        column-count: 1
        column-gap: 0cm
        .dnd-table-container
            display: block
            margin: 16px auto
            max-width: -moz-fit-content
            max-width: fit-content
            overflow-x: auto
            white-space: nowrap
            scrollbar-color: var(--rule-red) var(--item-table-pink)
            scrollbar-width: auto
            &::-webkit-scrollbar
                height: 15px
            /* Track */
            &::-webkit-scrollbar-track
                background: var(--item-table-pink)
            &::-webkit-scrollbar-thumb
                background: var(--rule-red)
                border-radius: 10px
                height: 15px
